}
.page-header__toggle {
  position: relative;
  width: 24px;
  height: 24px;
  padding: 0;
  cursor: pointer;
  background-color: #ffffff;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__burger,
.page-header__burger::before,
.page-header__burger::after {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.page-header__burger::before,
.page-header__burger::after {
  content: "";
}
.page-header__burger::before {
  transform: translateY(-8px);
}
.page-header__burger::after {
  transform: translateY(8px);
}
.page-header__toggle--active .page-header__burger {
  height: 0;
}
.page-header__toggle--active .page-header__burger::before {
  transform: rotate(45deg);
}
.page-header__toggle--active .page-header__burger::after {
  transform: rotate(-45deg);
}
