  padding-top: 12px;
  padding-bottom: 12px;
}
.form__label--radio {
  position: relative;
  display: block;
  cursor: pointer;
}
.form__control--radio + .form__label--radio::before {
  content: "";
  position: absolute;
  top: -6px;
  left: -45px;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__control--radio + .form__label--radio::before {
    left: -60px;
    width: 40px;
    height: 40px;
  }
}
.form__control--radio:checked + .form__label--radio::after {
  content: "";
  position: absolute;
  top: 2px;
  left: -37px;
  width: 14px;
  height: 14px;
  background: #68b738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__control--radio:checked + .form__label--radio::after {
    top: 5px;
    left: -49px;
    width: 18px;
    height: 18px;
  }
}
.form__control--radio + .form__label--radio:hover::before {
  border-color: #bababa;
}
.form__control--radio + .form__label--radio:focus::before {
  border-color: #bababa;
}
.form__control--radio + .form__label--radio:active {
  border-color: #bababa;
  opacity: 0.3;
}
.form__control--radio:focus + .form__label--radio::before {
  outline: 2px solid #000000;
}
.form__control--radio:disabled + .form__label--radio,
.form__control--radio:disabled + .form__label--radio::before,
.form__control--radio:disabled + .form__label--radio:hover::before,
.form__control--radio:disabled + .form__label--radio:focus::before,
.form__control--radio:disabled + .form__label--radio::after {
  opacity: 0.3;
  cursor: default;
}
